#about{
    padding: 2rem;
}
.about_container{
    display: grid;
    grid-template-columns: 45% 50%;
    gap: 15%;
    align-items: center;
}

.about_me{
    width: 90%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;
}

.about_me_img{
    overflow: hidden;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about_me_img:hover{
    transform: rotate(0deg);
}

/* .about_content{
    clip-path: polygon(100% 0%, 100% 50%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);;
} */

.about_content h1{
    color: var(--color-primary);
    letter-spacing: -1px;
}

.about_content h1 span{
font-size: 60%;
}

.about_content h4{
color: var(--color-light);
}
.about_content p{
margin: 2rem 1.5rem;
}


@media screen and (max-width:600px){
    .about_container{
        display: grid;
        grid-template-columns: 1fr;
        gap: 3%;
    }
    .about_me{
        width: 60%;
        margin: 1rem auto 1rem;
        border-radius: 40%;
    }

    .about_me_img{
        border-radius: 40%;
    }
    
    .about_content{
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }

    .about_content p{
        margin: 0.5rem 1.5rem;
        text-align: left;
        }
        .about_content h1{
            font-size: 30px;
            }

            .about_content p li{
                list-style: circle;
            }

}