@import url("https://fonts.googleapis.com/css2?family=Satisfy&display=swap");

header {
  height: 100vh;
  /* padding-top: 3rem; */
  overflow: hidden;
  background: transparent;
  position: relative;
}
header::before {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.header_container {
  height: 100%;
  text-align: center;
  text-align: center;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header_container h1 {
  color: rgb(7, 221, 175);
  font-family: "Satisfy", cursive;
  font-size: 60px;
}
.header_container h1::first-letter {

  font-size: 120%;
  font-weight: 600;
}

.header_container h3::first-letter {
  color: red;
  font-size: 1.5rem;
  font-weight: 600;
}

.cta {
  margin-top: 5rem;
  display: flex;
  gap: 1.5rem;
  justify-content: center;
}
.me {
  margin-top: 3rem;
  position: relative;
  text-align: center;
}

.me img {
  width: 40rem;
  height: 40rem;
  position: absolute;
  border-radius: 2rem 0 2rem 0;
  left: 50%;
  transform: translateX(-50%);

  /* clip-path: polygon(17% 0, 100% 0%, 86% 100%, 0% 100%); */
}

.header_socials {
  align-items: center;
  position: absolute;
  left: 0;
  bottom: 3rem;
  display: flex;
  flex-direction: column;
}
.header_socials::after {
  content: "";
  width: 2px;
  height: 2rem;
  background-color: var(--color-primary);
}
.scroll-down {
  position: absolute;
  bottom: 3rem;
  right: 0;
  transform: rotate(90deg);
}

/* ====================  Media Queries ================== */

@media screen and (max-width: 1024px) {
  header {
    height: 80vh;
  }
}

@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }
  .header_socials,
  .scroll-down {
    display: none;
  }
  
  .cta{
    padding-top: 1rem;
  }
}
