#contact{
    padding: 2rem;
    margin: 2rem auto;
    width: 86%;
    text-align: center;
}

.contact_container{
    width: 100%;
    margin: 1rem auto;
    display: grid;
    grid-template-columns: 17% 65%;
    gap: 10%;
    justify-content: center;
}

.contact_cards_container{
    display: flex;
    flex-direction: column;
}

.contact_card{
    width: 100%;
    margin: 1rem auto;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1.5rem 1.5rem 1.5rem 0;
    transition: var(--transition);
}

.contact_card:hover{
    background-color: transparent;
    border: 1px solid var(--color-primary-variant);
}


.contact_icon{
    font-size: 30px;
    margin-bottom: 0.5rem;
}

.form{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.2rem;
    padding: 3rem;
    width: 100%;
}

input, textarea{
    width: 100%;
    padding: 1rem 1.5rem;
    border-radius: 0.5rem;
    outline: none;
    background-color: transparent;
    color: var(--color-white);
    border: 1px solid var(--color-primary-variant);
    resize: none;
}

/* ======================= Media Queries ============= */

@media screen and (max-width:1024px) {
    .contact_container{
        width: 100%;
        grid-template-columns: 1fr;
        gap: 1rem;
    }
    .contact_cards_container{
        flex-direction: row;
        gap: 1rem;        
    }
}

@media screen and (max-width:600px) {
    #contact{
        width: 100%;
    }
    .contact_cards_container{
        flex-direction: row;
        gap: 0.5rem;       
    }
    .form{
        padding: 1rem;
    }
}